.card {
  border: solid 1px rgb(154, 152, 152);
  border-radius: 2px;
  width: 200px;
  height: 150px;
  margin: 15px 10px 15px 0px;
  position: relative;
  -webkit-box-shadow: 4px 4px 10px rgb(57, 56, 56);
          box-shadow: 4px 4px 10px rgb(57, 56, 56);
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
  
.img-container {
  height: 100%;
  overflow: hidden;
}
  
img {
  width:auto;
  height:auto;
  width: 100%;
  height:150px;
}
  
.card:hover {
  -webkit-box-shadow: 8px 8px 20px rgb(56, 56, 56);
          box-shadow: 8px 8px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}
  
.card:active {
  -webkit-transform: translate(1px, 1px);
      -ms-transform: translate(1px, 1px);
          transform: translate(1px, 1px);
}


